.search-form {
  width: 600px;
  margin: 0 auto 40px;
  display: flex;
}

.search-input {
  height: 40px;
  border: 1px solid #303030;
  flex-grow: 1;
  padding-left: 15px;
  font-size: 18px;
}

.search-btn {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #2ecc71;
  border: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 14px;
}

.weather__card {
  width: 300px;
  padding: 20px;
  box-shadow: 0 0 10px #ccc;
}

.weather-info-item:not(:last-child) {
  margin-bottom: 10px;
}

.city-name {
  text-align: center;
  text-transform: uppercase;
  margin-bottom: 10px;
  font-size: 26px;
}

